/* CartPage.css */

.cart-page {
  background-color: #fff9e6; /* Light honey color */
  color: #4d3300; /* Dark brown text color for contrast */
  padding: 40px 20px; /* Increased padding for better spacing */
  min-height: 100vh;
  box-sizing: border-box; /* Ensures padding is included in total height */
}

.cart-page h1 {
  color: #f5b61f; /* Honey yellow color */
  text-align: center;
  margin-bottom: 40px; /* Increased margin for separation */
  font-size: 2em;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); /* Subtle shadow for text */
}

.cart-items {
  display: flex;
  flex-direction: column;
  gap: 30px; /* Increased gap between items */
  padding-top: 25px; /* Increased padding to push items down */
}

.cart-item {
  display: flex;
  align-items: center;
  background-color: #fff; /* White background for items */
  border: 2px solid #f5b61f; /* Honey yellow border */
  border-radius: 12px; /* More rounded corners */
  padding: 15px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Enhanced shadow for modern look */
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth transition for hover effects */
}

.cart-item:hover {
  transform: translateY(-5px); /* Slight lift effect on hover */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Enhanced shadow on hover */
}

.cart-item-image {
  width: 120px; /* Adjusted size for better visibility */
  height: auto;
  border-radius: 12px;
  object-fit: cover; /* Ensure image fits well */
  margin-right: 20px;
}

.cart-item-details {
  flex: 1;
}

.cart-item-details h2 {
  margin: 0 0 10px;
  font-size: 1.5em; /* Increased font size for item names */
}

.cart-item-details p {
  margin: 5px 0;
}

/* Honeycomb-inspired button styles */
.total-button,
.checkout-button {
  background: linear-gradient(
    135deg,
    #f5b61f,
    #e4a01f
  ); /* Gradient honey yellow */
  color: #fff;
  border: 2px solid #e4a01f; /* Darker honey yellow border */
  border-radius: 50%; /* Circular shape */
  padding: 10px;
  width: 60px; /* Diameter of the button */
  height: 60px; /* Diameter of the button */
  font-size: 0.8em; /* Adjust font size */
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow for a floating effect */
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s; /* Smooth transition for hover effects */
  margin: 20px auto; /* Center align button with margin */
}

.cart-total {
  margin-top: 40px; /* Increased margin for separation */
  text-align: center;
  font-size: 1.8em; /* Larger font size for emphasis */
  font-weight: bold;
}
